<template>
  <div v-for="value in menu" :key="value.name" class=" flex flex-row align-center w-full">
    <div v-for="item in value.standings" :key="item.name" class="w-full">
      <div v-for="(items,index) in item.standings" :key="items.name">
        <table class="w-full">
          <tbody class="flex flex-row align-center justify-around">
          <tr class="my-2 w-5/12">
            <th>
              <div class="flex flex-row align-center justify-start ml-2">
                <span class="text-slate-800 m-2">{{index+=1}}</span>
                <span><img @error="notShow" :src="`${items.team.logo}`" alt="" class="w-7 h-7 rounded-full m-2"></span>
                <span class="text-slate-800 font-thin m-2" style="font-size: 13px">{{items.team.name_fa}}</span>
              </div>
            </th>
          </tr>
          <tr class="flex flex-row justify-between w-6/12">
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.match_played}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.win}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.draw}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.lose}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.goal_scored - items.goal_against}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.goal_scored}} - {{item.goal_againset}}</span></td>
            <td class=" text-right ml-3"><span class="font-thin text-slate-800">{{items.overall_point}}</span></td>
          </tr>
          </tbody>
        </table>

      </div>
    </div>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "RankTeams",
  props:{
    menu:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      notFound,notShow,
    }
  }
}
</script>

